                    ],
                    presellCryType: [
                        { required: true, message: '请选择定金币种', trigger: 'change' }
                    ],
                    amount: [
                        { required: true, message: '请输入商品数量', trigger: 'blur' },
                        { type: 'number', message: '单价必须为数字'}
                    ],
                    payDate: [
                        { required: true, message: '请输入支付日期', trigger: 'blur' }
                    ],
                    payCurrency: [
                        { required: true, message: '请选择收款方式', trigger: 'blur' }
                    ],
                    bfyName: [
                        { required: true, message: '请输入汇款人姓名', trigger: 'blur' }
                    ],
                    bfyBankName: [
                        { required: true, message: '请输入汇款银行', trigger: 'blur' }
                    ],
                    bankCode: [
                        { required: true, message: '请输入汇款银行卡号', trigger: 'blur' },
                        { min: 10, max: 30, message: '长度在 10 到 30 个字符', trigger: 'blur' }
                    ],
                    bankAddress: [
                        { required: true, message: '请输入银行地址', trigger: 'blur' }
                    ],
                    swiftCode: [
                        { required: true, message: '请输入银行代码', trigger: 'blur' }
                    ],
                    bfyAccountNumber: [
                        { required: true, message: '请输入汇款人账号', trigger: 'blur' }
                    ],
                    bfyAddress: [
                        { required: true, message: '请输入汇款人地址', trigger: 'blur' }
                    ],
                    btcAddress: [
                        { required: true, message: '请输入BTC汇款地址', trigger: 'blur' }
                    ],
                    needPay: [
                        { required: true, message: '请输入付款金额/payment amount', trigger: 'blur' },
                        { type: 'number', message: '单价必须为数字'}
                    ],
                    isHostOrder: [
                        { required: true, message: '请选择交付方式', trigger: 'blur' }
                    ],
                    area: [
                        { required: true, message: '请选择交付地区', trigger: 'blur' }
                    ],
                    mobilephone: [
                        { required: true, message: '请填写手机号', trigger: 'blur' }
                    ],
                    receiver: [
                        { required: true, message: '请填写收件人', trigger: 'blur' }
                    ],
                    // 国内参数
                    phonePrefix: [
                        {message: '请填写电话区号', trigger: 'blur' }
                    ],
                    phoneNumber: [
                        { message: '请填写电话', trigger: 'blur' }
                    ],
                    provinceId: [
                        { required: true, message: '请选择省/直辖市', trigger: 'change' }
                    ],
                    cityId: [
                        { required: true, message: '请选择城市', trigger: 'change' }
                    ],
                    regionId: [
                        { required: true, message: '请选择区/县', trigger: 'change' }
                    ],
                    streetId: [
                        {required: true,  message: '请选择乡镇/街道', trigger: 'change' }
                    ],
                    address: [
                        { required: true, message: '请填写详细地址', trigger: 'blur' }
                    ],
                    zip: [
                        { required: true, message: '请填写邮编', trigger: 'blur' }
                    ],
                    remarks: [
                        { required: false, message: '请填写备注', trigger: 'blur' }
                    ],
                    email: [
                        { required: true, message: '请填写邮箱', trigger: 'blur' }
                    ],
                    cryptocurrency: [
                        { required: true, message: '请选择币种', trigger: 'change' }
                    ],
                    walletAddress: [
                        { required: true, message: '请填写钱包地址', trigger: 'blur' },
                        { min: 10, message: '长度不低于10个字符', trigger: 'blur' },
                        { pattern: /^[A-Za-z0-9]+$/, message: '不允许输入空格汉字' }
                    ],
                    miningPool: [
                        { required: true, message: '请选择矿池', trigger: 'change' }
                    ],
                    // 国外参数
                    Address1: [
                        { required: false, message: '请填写第一个地址', trigger: 'blur' }
                    ],
                    Address2: [
                        { required: false, message: '请填写第二个地址', trigger: 'blur' }
                    ],
                    City: [
                        { required: false, message: '请填写城市', trigger: 'blur' }
                    ],
                    State: [
                        { required: false, message: '请填写省/州', trigger: 'blur' }
                    ],
                    Country: [
                        { required: false, message: '请填写国家', trigger: 'blur' }
                    ]
                }
            }
        },
        components: {
            offlineUserInfo // 注册子组件
        },
        methods: {
            amountBlur(){
                this.ruleForm.totalPrice = this.ruleForm.amount * this.ruleForm.unitPrice
                this.ruleForm.totalPresellPrice = this.ruleForm.amount * this.ruleForm.presellPrice
            },
            statusChange(val){
                console.log(val)
            },
            hostchange(val){
                console.log(val)
                if(val == 1){
                    this.$set(this.rules.phonePrefix[0], "required", true)
                    this.$set(this.rules.phoneNumber[0], "required", true)
                }else {
                    this.$set(this.rules.phonePrefix[0], "required", false)
                    this.$set(this.rules.phoneNumber[0], "required", false)
                }
            },
            productChange(val){
                // console.log(val)
                this.warning = '可发货托管'
                // sellType 1只发货，2只托管，3都可
                this.selltype = this.ruleForm.product.split('/')[2]
                // console.log(this.selltype)
                if(this.selltype == 1){
                    this.ruleForm.isHostOrder = 0
                    this.warning = '可发货'
                }else if(this.selltype == 2){
                    this.ruleForm.isHostOrder = 1
                    this.warning = '可托管'
                }
            },
            changeArea(val){
                // console.log(this.ruleForm.cityId)
                this.ruleForm.cityId = ''
                this.ruleForm.regionId = ''
                this.ruleForm.streetId = ''
                this.regionArr = []
                this.streetArr = []
                this.getCity()
            },
            changeCity(val){
                this.ruleForm.regionId = ''
                this.ruleForm.streetId = ''
                this.streetArr = []
                this.getRegion()
            },
            changeRegion(val){
                this.ruleForm.streetId = ''
                this.getStreet()
            },
            changeStreet(val){

            },
            cryptocurrencyChange(val){
                // console.log(this.ruleForm.cryptocurrency)
                if(this.ruleForm.cryptocurrency == 'ETH'){
                    this.optionsPool = [{
                        value: 'eth.pandaminer.com:81',
                        label: 'eth.pandaminer.com:81'
                    }]
                }else{
                    this.optionsPool = [{
                        value: 'eth.pandaminer.com:81',
                        label: 'eth.pandaminer.com:81'
                    }, {
                        value: 'zcash.flypool.org',
                        label: 'zcash.flypool.org'
                    }, {
                        value: 'waterhole.io',
                        label: 'waterhole.io'
                    }]
                }
            },
            submitForm(formName) {
                // console.log(this.ruleForm.Country)
                this.idArr = this.ruleForm.product.split('/')
                // let params = this.ruleForm
                // params.uid = this.accountId
                // params.productId = this.idArr[0]
                // params.productAttrId = this.idArr[1]
                // params.deliverStartTime = this.ruleForm.payDate[0]/1000
                // params.deliverEndTime = this.ruleForm.payDate[1]/1000
                // params.lang = this.lang
                // params.isDefault = true
                // params.notice = '订单录入成功'
                // delete params.product;
                // delete params.payDate;
                // console.log(params)
                // return
                // console.log(this.ruleForm.presellCryType)
                this.$refs[formName].validate((valid) => {
                    // console.log(valid)
                    if (valid) {
                        this.$api('/admin/offline/add_offline_order', {
                            totalPresellPrice: this.ruleForm.totalPresellPrice,
                            totalPrice: this.ruleForm.totalPrice,
                            uid: this.accountId, // 用户id
                            tradeType: this.ruleForm.tradeType, //交易类型 1 全款 2 定金
                            productId: this.idArr[0],  // productId/attrId, //商品id
                            productAttrId: this.idArr[1],    // 商品规格id
                            unitPrice: this.ruleForm.unitPrice, // 单价
                            currencyType: this.ruleForm.currencyType,  //货币类型
                            presellPrice: this.ruleForm.presellPrice, // 定金
                            presellCryType: this.ruleForm.presellCryType, // 定金货币类型
                            amount: this.ruleForm.amount,  //数量台
                            deliverStartTime: this.ruleForm.payDate[0]/1000,  // 部署开始时间
                            deliverEndTime: this.ruleForm.payDate[1]/1000, // 部署结束时间
                            status: this.ruleForm.status, //订单状态
                            payCurrency: this.ruleForm.payCurrency,
                            bfyName: this.ruleForm.bfyName,
                            bfyBankName: this.ruleForm.bfyBankName,
                            bankCode: this.ruleForm.bankCode,
                            bankAddress: this.ruleForm.bankAddress,
                            swiftCode: this.ruleForm.swiftCode,
                            bfyAccountNumber: this.ruleForm.bfyAccountNumber,
                            bfyAddress: this.ruleForm.bfyAddress,
                            btcAddress: this.ruleForm.btcAddress,
                            needPay: this.ruleForm.needPay,
                            isHostOrder: this.ruleForm.isHostOrder,
                            lang: this.lang,
                            receiver: this.ruleForm.receiver,
                            phonePrefix: this.ruleForm.phonePrefix,
                            phoneNumber: this.ruleForm.phoneNumber,
                            mobilephone: this.ruleForm.mobilephone,
                            zip: this.ruleForm.zip,
                            countryId: this.ruleForm.Country,
                            isDefault: true,
                            provinceId: this.ruleForm.provinceId,
                            cityId: this.ruleForm.cityId,
                            regionId: this.ruleForm.regionId,
                            streetId: this.ruleForm.streetId,
                            address: this.ruleForm.address,
                            address1: this.ruleForm.Address1,
                            address2: this.ruleForm.Address2,
                            city: this.ruleForm.City,
                            state: this.ruleForm.State,
                            email: this.ruleForm.email,
                            walletAddress: this.ruleForm.walletAddress,
                            miningPool: this.ruleForm.miningPool,
                            remarks : this.ruleForm.remarks,
                            cryptocurrency: this.ruleForm.cryptocurrency,
                            notice: '订单录入成功'
                        }).then(res => {
                            // console.log(res)
                        });
                        this.warning = ''
                        this.cityArr = []
                        this.regionArr = []
                        this.streetArr = []
                        this.ruleForm.mobilephone = ''
                        this.$refs[formName].resetFields();
                    } else {
                        console.log('error submit!!');
                return false;
            }
            });
            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
            },
            tabClick(val){
                console.log(this.ruleForm.isHostOrder)
                this.lang = val.name
                // console.log(this.lang)
                if(val.label == '国内'){
                    this.ruleForm.Country = 48
                    // 国内
                    // this.$set(this.rules.phoneNumber[0], "required", true)
                    this.$set(this.rules.mobilephone[0], "required", true)
                    this.$set(this.rules.provinceId[0], "required", true)
                    this.$set(this.rules.cityId[0], "required", true)
                    this.$set(this.rules.regionId[0], "required", true)
                    this.$set(this.rules.streetId[0], "required", true)
                    this.$set(this.rules.address[0], "required", true)
                    // 国外
                    this.$set(this.rules.phonePrefix[0], "required", false)
                    this.$set(this.rules.phoneNumber[0], "required", false)
                    this.$set(this.rules.Address1[0], "required", false)
                    this.$set(this.rules.Address2[0], "required", false)
                    this.$set(this.rules.City[0], "required", false)
                    this.$set(this.rules.State[0], "required", false)
                    this.$set(this.rules.Country[0], "required", false)
                }else if(val.label == '国外'){
                    // 国内
                    this.$set(this.rules.mobilephone[0], "required", false)
                    this.$set(this.rules.provinceId[0], "required", false)
                    this.$set(this.rules.cityId[0], "required", false)
                    this.$set(this.rules.regionId[0], "required", false)
                    this.$set(this.rules.streetId[0], "required", false)
                    this.$set(this.rules.address[0], "required", false)
                    // 国外
                    this.$set(this.rules.phonePrefix[0], "required", true)
                    this.$set(this.rules.phoneNumber[0], "required", true)
                    this.$set(this.rules.Address1[0], "required", true)
                    this.$set(this.rules.Address2[0], "required", true)
                    this.$set(this.rules.City[0], "required", true)
                    this.$set(this.rules.State[0], "required", true)
                    this.$set(this.rules.Country[0], "required", true)

                }
                if(val.label == '国内' && this.ruleForm.isHostOrder == 1){
                    this.$set(this.rules.phonePrefix[0], "required", true)
                    this.$set(this.rules.phoneNumber[0], "required", true)
                }
            },
            getProductList(){
                this.$api('/admin/product/list', {
                    status: '1,2,3,5',
                    pageNo: 0,
                    pageSize: 100
                }).then(res => {
                    // console.log(res)
                    let d = res.data
                    this.productList = d.list
                this.productList.forEach((item, index) => {
                    if(item.DefaultAttr.InStock <= 0){
                    this.productList.splice(index, 1)
                }
            })
            });
            },
            getAllCountry(){
                this.$api('/admin/default/get_all_country', {}).then(res => {
                    // console.log(res)
                    this.countryArr = res.data
            });
            },
            getAllArea(){
                this.$api('/admin/default/get_all_area', {}).then(res => {
                    // console.log(res)
                    // let d = res.data
                    this.areaArr = res.data
            });
            },
            getCity(){
                this.$api('/admin/default/get_city', {
                    provinceId: this.ruleForm.provinceId
                }).then(res => {
                    // console.log(res)
                    // let d = res.data
                    this.cityArr = res.data
            });
            },
            getRegion(){
                this.$api('/admin/default/get_region', {
                    cityId: this.ruleForm.cityId
                }).then(res => {
                    // console.log(res)
                    // let d = res.data
                    this.regionArr = res.data
            });
            },
            getStreet(){
                this.$api('/admin/default/get_street', {
                    regionId: this.ruleForm.regionId
                }).then(res => {
                    console.log(res)
                // let d = res.data
                this.streetArr = res.data
                if(this.streetArr == 0){
                    this.$set(this.rules.streetId[0], "required", false)
                }else {
                    this.$set(this.rules.streetId[0], "required", true)
                }
            });
            }
        },
        created(){
            this.accountId = this.$route.query.accountId
            this.getProductList()
            this.getAllCountry()
            this.getAllArea()
        }
    }
</script>
<style lang='scss' scoped>
    @import "../../assets/css/titlecom.scss";
    .input_msg{
        margin: 30px 0 0 30px;
        width: 96%
    }
    .header{
        height: 50px;
        font-size: 18px;
        line-height: 50px;
        padding-left: 5px;
        border-bottom: 1px solid #ebeef5;
        margin-bottom: 20px;
        font-weight: 600
    }
    .currencyTable{
        margin-left: 95px;
        margin-bottom: 20px;
        font-size: 17px
    }
    .currencyTable tr td{
        border: 1px solid #999999
    }
    .currencyTable tr td:first-child{
        font-weight: 600;
        width: 150px;
        height: 30px;
        padding-left: 15px
    }
    .currencyTable tr td:nth-child(2){
        width: 400px;
        padding-left: 15px
    }
</style>


